body {
    padding-top: 60px;
    padding-bottom: 40px;
}

.navbar-default {
    background: #00618B;   
}

.navbar-default .navbar-nav > li > a {
  color: #FFF;
  text-shadow: none;
}

.navbar-default .navbar-nav > li > a:hover,
.navbar-default .navbar-nav > li > a:focus {
  color: #FFF;
  background-color: #408AA7;
}

.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:hover,
.navbar-default .navbar-nav > .active > a:focus {
  color: #FFF;
  background: #408AA7;
}

.navbar-default .navbar-nav > .open > a,
.navbar-default .navbar-nav > .open > a:hover,
.navbar-default .navbar-nav > .open > a:focus {
  color: #FFF;
  background-color: #408AA7;
}

.navbar-default .navbar-brand:hover,
.navbar-default .navbar-brand:focus {
  color: #FFF;
  background-color: transparent;
}


div.container a.navbar-brand {
    background: url("../img/Logo_De_Eekhoorn_homepageNW_2.png") no-repeat scroll 0 0 transparent;
	background-size: auto 100%;
    margin-left: 0;
    padding: 14px 20px 12px 200px;
    color: #FFF;
}

// Standard Forms
.form-standard {
    margin: 0 auto;
    max-width: 640px;
    padding: 15px;
}

// Login Form
.form-login {
    margin: 0 auto;
    max-width: 330px;
    padding: 15px;    
    .form-control {
        box-sizing: border-box;
        font-size: 16px;
        height: auto;
        padding: 10px;
        position: relative;        
    }
    
}
